<script lang="ts">let todos = [];
let answer = '';
let text = '';
let question = '';
export {};
</script>

<style>
    .complete{
        text-decoration: line-through;
    }
</style>

<div>Question</div>

<input bind:value={question}/>

<div>Question: {question}</div>
<div>Answer: {asnwer}</div>
<button on:click={()=>{
    asnwer = 'AI generated prompt goes here'
}}>Answer</button>



<form 
    on:submit|preventDefault={() =>{
        todos = [{text, completed: false}, ...todos]
        text = '';
    }}>
    <input bind:value={text}/> 
</form>

<ul>
    {#each todos as todo (todo.text)}
        <li
        class:complete={todo.completed}
            on:click={()=>{
                todo.completed = !todo.completed;
            }}>
            {todo.text}
        </li>
    {/each}
</ul>

<button on:click={()=>{
        tsvscode.postMessage({type: 'onInfo',value: 'info message'});
    }}>click me</button>

<button on:click={()=>{
        tsvscode.postMessage({type: 'onError',value: 'error message'});
    }}>click me for error</button>

